export default function Button() {
  const isClear = true
  return(
    <div className={isClear ? "clear-button" : "black-button"}>
      <a className="title">{props.children}</a>
      <div className="button-clear"/>
      <style jsx>{`
        .clear-button{
          background: white
        }
        .black-button{
          background: black
        }
        `}</style>
    </div>
  )
}
